import React, {Component} from 'react';
import {NavLink} from 'react-router-dom';

import './Navegacion.css';

class Navegacion extends Component{


    render(){

        return(
            <nav className="navbar navbar-expand-sm justify-content-center">

                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#collapsibleNavbar">
                    <span className="navbar-toggler-icon"><img className="img-fluid" src="/img/icon-phone.png"  alt=""/></span>
                </button>

                <div className="collapse navbar-collapse justify-content-center" id="collapsibleNavbar">

                    <ul className="navbar-nav maxancho">

                        <li className="nav-item">
                            <NavLink 
                                className="nav-link" activeClassName="activo" to={'/informacion'}>Información
                            </NavLink>
                        </li>

                        <li className="nav-item">
                            <NavLink 
                                className="nav-link" activeClassName="activo" to={'/cacharreo'}>Cacharreo
                            </NavLink>
                        </li>

                    </ul>                    
                </div>
            </nav>
        )
    }

}

export default Navegacion;


