import React,{Component} from 'react';
import { Link } from 'react-router-dom';
import YouTube from 'react-youtube';

// redux
import { connect } from 'react-redux';
import { mostrarUno } from '../actions/cacharreoActions';

import './Publicacion.css';

class Publicacion extends Component {

    //monto la informacion de cada post
    componentDidMount(){
        // capturo el id en los props que vienen
        const { id } = this.props.match.params;
        this.props.mostrarUno(id);

    }

    _onReady(event) {
        // access to player in all event handlers via event.target
        event.target.pauseVideo();
        //event.target.playVideo();
      }
    

    render(){
        if(!this.props.cacharreo) return null;
        console.log(this.props.cacharreo)

        const {id, descripcion1,descripcion2,fecha, tags,link1, imagen1, titulo,video} = this.props.cacharreo;

        //let quitocomas = tags.split(',').join(''); // los datos vienen separados por comas, asi se las quito

        let texto1;
        let texto2;
        let links;
        let img1;

        // aquí voy a validar que si haya datos
        if( descripcion1 === ""){
            texto1 = null;
        }else{
            texto1 = descripcion1;
        }

        if( descripcion2 === ""){
            texto2 = null;
        }else{
            texto2 = <p>{`${descripcion2}`}</p>;
        }

        // valores default de los videos
        const opts = {
            height: '390',
            width: '640',
            playerVars: { 
              autoplay: 1
            }
          };
    
        // si tiene video muestrelo sino muestre imagen
        if( video !== " "){
            img1 =  <div className="embed-responsive embed-responsive-16by9 mt-4">
                        <YouTube
                            videoId={video} // url video
                            className="embed-responsive-item"
                            opts={opts}
                            onReady={this._onReady}
                        />
                    </div>;
        } else {
            img1 = <img src={`${imagen1}.jpg`} className="img-fluid float-left" alt="..." />;
        }

        // si tiene links muestrelos sino no muestre nada
        if(link1 !== ""){
            links = <p> puedes obtener mas información <a className="publi-linkexterno" href={`${link1}`} target="_blank">aquí</a></p>;
        }else{
            links = null;
        }

        return(
            
                // si el servidor esta caido 
                this.props.cacharreo === null ?
                    <div className="text-center my-2"><p> El servidor esta caido</p><p>Por favor vuelve mas tarde</p></div>
                :
                // si hay respuesta muestro datos
                <div className="row mt-5">
                    <div className="col-sm-12">
                        <h2 className="publica-titu">{`${titulo}`}</h2>
                        <span className="publica-fecha">{`${fecha}`}</span>
                    </div>
                    <div className="col-12 col-sm-12 col-md-5">
                        {img1}
                        <div className="clearfix"></div>
                        {/* <p className="publica-tags text-left pt-2">{`${quitocomas}`}</p> */}
                    </div>
                    <div className="col-12 col-sm-12 col-md-7">
                        <p className="mt-4">{`${texto1}`}</p>
                        {texto2}
                        {links}
                    </div>
                    <div className="col-sm-12">
                        <div className="mt-3 mover"><Link className="publica-links mt-2" to={'/cacharreo'}>Volver atras</Link></div>
                    </div>
                </div>
        )
    }
}

//state
const mapStateToProps = state => ({
    cacharreo: state.cacharreos.cacharreo
})


export default connect ( mapStateToProps,{ mostrarUno })(Publicacion);


