import React, { Component } from 'react';

// componentes
import Postcacharreo from './Postcacharreo';

// redux
import { connect } from 'react-redux';

// actions
import { mostrarTodo } from '../actions/cacharreoActions';

import './Cacharreo.css';

class Cacharreo extends Component {

    componentDidMount(){
        // Ejecuto la action mostrar productos
        this.props.mostrarTodo();
    }

    render() { 
        const { cacharreos } = this.props;
        //console.log(this.props);

        // si el serve se cayo
        if(cacharreos === undefined) return null;

        return ( 
            
            <section className="row hiddeScroll">
                <div className="col-sm-12">

                    {
                        // si el serve esta caido respondo con esto
                        cacharreos.length === 0 ?
                        <div className="text-center my-2"><p> El servidor esta caido</p><p>Por favor vuelve mas tarde</p></div>
                        :
                        // si hay datos los muestro
                        cacharreos.map( post => (
                            <Postcacharreo 
                                key={post.id}
                                data={post}
                            />
                        ) )

                    }
                    
                </div>
            </section>


         );
    }
}

// state
const mapStateToProps = state => ({
    cacharreos: state.cacharreos.cacharreos
})
 
export default connect(mapStateToProps, { mostrarTodo })(Cacharreo);


