import React, {Component} from 'react';

// redux
import { Provider } from 'react-redux';
import store from '../store';

// router
import { BrowserRouter, Route, Switch } from 'react-router-dom';

// componentes
import Header from './Header';
import Navegacion from './Navegacion';
import Informacion from './Informacion';
//import Contacto from './Contacto';
import Cacharreo from './Cacharreo';
import Publicacion from './Publicacion';
import Footer from './Footer';

import './Router.css'

class Router extends Component {

    render(){
        return(
            <Provider store={store}>
            <BrowserRouter>
               
                    <div className="container"> 

                        <Header 
                            oficio='Desarollo Web'
                            autor='Johnny Sepúlveda'
                        />

                        <Navegacion 
                           
                        />

                        <Switch>
                            
                            <Route exact path="/" component={Cacharreo} />
                            <Route exact path="/cacharreo" component={Cacharreo} />
                            <Route exact path="/informacion" component={Informacion} />
                            <Route exact path="/publicacion/:id" component={Publicacion}/>
                            <Route component={Error} />
                            {/*  
                            <Route exact path="/contacto" component={Contacto} />
                            */}

                        </Switch>

                        <Footer />

                    </div>
                
            </BrowserRouter>
            </Provider>
            
        );
    }
}

export default Router;


