import { MOSTRAR_TODO, MOSTRAR_UNPOST } from './types';

// me permite reemplazar el fecth para facilitar las peticiones
import axios from 'axios';

//GET para mostrar toda la info
export const mostrarTodo = () => async dispatch => {
    const res = await axios.get('https://my-json-server.typicode.com/Kaziuz/cacharreos/cacharreos');
    dispatch({
        type: MOSTRAR_TODO,
        payload: res.data
    })   
}

// GET para mostrar solo un producto
export const mostrarUno = id => async dispatch => {
    const res = await axios.get(`https://my-json-server.typicode.com/Kaziuz/cacharreos/cacharreos/${id}`);
    dispatch({
        type: MOSTRAR_UNPOST,
        payload: res.data
    })
}




