import React, {Component} from 'react';
import './Informacion.css';

class Informacion extends Component {
    
    render() { 
        return ( 
            <section className="row">
                <div className="col-sm-12">
                    <div className="text-center mt-2">
                        <img className="img-fluid" src="/img/kaziuz-live.jpg" alt="logo imagen" />
                    </div>
                    <div className="descripcion">
                        <p>
                            Me considero una persona prudente y sencilla, todos los días estoy aprendiendo algo nuevo. 
                            Amante a los videojuegos, la música y la tecnología. Me gusta 
                            trabajar en grupo. Conciente de que los grandes proyectos  se logran
                            con trabajo constante y paciencia. Me gusta el ciber punk, hacer música, salir a caminar y ver películas
                            de ciencia ficción.
                        </p>
                    </div>    
                </div>
            </section>
         );
    }
}
 
export default Informacion;


