import React, {Component} from 'react';

import './Footer.css';

class Footer extends Component{
    
    render(){
        return(
            <footer className="row">
                <div className="col-12 col-sm-12">
                    <div className="foo-env">
                        <a href="https://kaziuz.bandcamp.com/" className="link-foo img-fluid" target="_blank"><div className="foo-img foo-bandcamp img-fluid"></div></a>
                        <a href="https://github.com/Kaziuz" className="link-foo img-fluid" target="_blank"><div className="foo-img foo-github img-fluid"></div></a>
                        <a href="mailto:johnnysepulveda988@gmail.com?Subject=Hola%20again" className="link-foo" target="_blank"><div className="foo-img foo-gmail img-fluid"></div></a>
                        <a href="#" className="link-foo img-fluid" target="_blank"><div className="foo-img foo-bitbucket img-fluid"></div></a>
                        <a href="https://twitter.com/kaziuz988" className="link-foo img-fluid" target="_blank"><div className="foo-img foo-twitter img-fluid"></div></a>
                    </div>
                </div>
            </footer>
        )
    }
}

export default Footer;




