import { MOSTRAR_TODO, MOSTRAR_UNPOST } from '../actions/types';

// state de este reducer
const stateInicial = { 
    cacharreos: [] 
}

export default function(state = stateInicial, action){
    // aqui devuelvo los nuevos states segun la action
    switch(action.type){
        
        // GET de todo
        case MOSTRAR_TODO:
            return{
                ...state,
                cacharreos: action.payload
            }
        // GET mostrar uno solo
        case MOSTRAR_UNPOST:
            return{
                ...state,
                cacharreo: action.payload
            }
        default:
            return state;
    }
}


