import React,{Component} from 'react';
import {Link} from 'react-router-dom';

import './Postcacharreo.css';

class Postcacharreo extends Component{

    pedirInformacion = id => {
        alert("pido informacion");
    }

    render(){
        //console.log(props.data)
        const {id, fecha,titulo} = this.props.data
        
        return(
        
            <React.Fragment>
                <article className="d-flex justify-content-center bloqueInfo">
                    <p className="text-left corroIzquierda anegro">{fecha}</p>
                    <h1 className="text-left titu-puplica">{titulo}</h1>
                    <p className="text-left corroIzquierda">
                        <Link to={`/publicacion/${id}`} className="links activado">Saber más</Link>
                    </p>
                </article>
            </React.Fragment>
        )
    }
}

export default Postcacharreo;


