// importamos combine reducer que va uniendo los reducer segun amos agregando mas
import { combineReducers } from 'redux';
// importo mi archivo donde tengo mis reducers
import cacharreoReducer from './cacharreoReducers';

// combino todos los reducers
export default combineReducers({
    cacharreos: cacharreoReducer
})


